	<div class="SideMenu">
		<el-aside class="home-aside" width="200px">
			<el-row>
				<el-col :span="24">
					<el-menu :default-active="activeMenu" :unique-opened="true">
						<router-link to="/store" v-if="role===''">
							<el-menu-item index="1">
								<i class="el-icon-document"></i>
								<span slot="title"></span>
							</el-menu-item>
						</router-link>

						<router-link to="/principal" v-if="role===''">
							<el-menu-item index="2">
								<i class="el-icon-document"></i>
								<span slot="title"></span>
							</el-menu-item>
						</router-link>

						<router-link to="/chart" v-if="role===''">
							<el-menu-item index="3">
								<i class="el-icon-document"></i>
								<span slot="title"></span>
							</el-menu-item>
						</router-link>

						<router-link to="/reception" v-if="role===''">
							<el-menu-item index="4">
								<i class="el-icon-document"></i>
								<span slot="title"></span>
							</el-menu-item>
						</router-link>

						<router-link to="/operator" v-if="role===''">
							<el-menu-item index="5">
								<i class="el-icon-document"></i>
								<span slot="title"></span>
							</el-menu-item>
						</router-link>

						<router-link to="/appointment" v-if="role==='' || role===''">
							<el-menu-item index="6">
								<i class="el-icon-document"></i>
								<span slot="title"></span>
							</el-menu-item>
						</router-link>

						<!-- <el-submenu index="7" v-if="role==='' || role===''">
							<template slot="title">
								<i class="el-icon-location"></i>
								<span></span>
							</template>

							<router-link to="/consume">
								<el-menu-item index="7-1">
									<i class="el-icon-document"></i>
									<span slot="title"></span>
								</el-menu-item>
							</router-link>

							<router-link to="/consume-change">
								<el-menu-item index="7-2">
									<i class="el-icon-document"></i>
									<span slot="title"></span>
								</el-menu-item>
							</router-link>
						</el-submenu> -->

						<el-submenu index="8" v-if="role==='' || role===''">
							<template slot="title">
								<i class="el-icon-location"></i>
								<span></span>
							</template>
							<router-link to="/customer">
								<el-menu-item index="8-1">
									<span slot="title"></span>
								</el-menu-item>
							</router-link>

							<router-link to="/customer-card">
								<el-menu-item index="8-2">
									<span slot="title"></span>
								</el-menu-item>
							</router-link>

							<router-link to="/customer-activity">
								<el-menu-item index="8-3">
									<span slot="title"></span>
								</el-menu-item>
							</router-link>

							<router-link to="/customer-card-change">
								<el-menu-item index="8-4">
									<span slot="title"></span>
								</el-menu-item>
							</router-link>
						</el-submenu>

						<el-submenu index="9" v-if="role==='' || role===''">
							<template slot="title">
								<i class="el-icon-location"></i>
								<span></span>
							</template>
							<router-link to="/stock-type">
								<el-menu-item index="9-1">
									<span slot="title"></span>
								</el-menu-item>
							</router-link>
							<router-link to="/stock">
								<el-menu-item index="9-2">
									<span slot="title"></span>
								</el-menu-item>
							</router-link>
							<router-link to="/stock-amount">
								<el-menu-item index="9-3">
									<span slot="title"></span>
								</el-menu-item>
							</router-link>
						</el-submenu>
						<el-submenu index="10" v-if="role==='' || role===''">
							<template slot="title">
								<i class="el-icon-location"></i>
								<span></span>
							</template>
							<router-link to="/project-type">
								<el-menu-item index="10-1">
									<span slot="title"></span>
								</el-menu-item>
							</router-link>
							<router-link to="/project">
								<el-menu-item index="10-2">
									<span slot="title"></span>
								</el-menu-item>
							</router-link>
						</el-submenu>

						<!-- <router-link to="/member-card" v-if="role==='' || role===''">
							<el-menu-item index="11">
								<i class="el-icon-document"></i>
								<span slot="title"></span>
							</el-menu-item>
						</router-link> -->

						<el-submenu index="12" v-if="role==='' || role===''">
							<template slot="title">
								<i class="el-icon-location"></i>
								<span></span>
							</template>
							<router-link to="/product-type">
								<el-menu-item index="12-1">
									<span slot="title"></span>
								</el-menu-item>
							</router-link>
							<router-link to="/product">
								<el-menu-item index="12-2">
									<span slot="title"></span>
								</el-menu-item>
							</router-link>
							<router-link to="/product-amount">
								<el-menu-item index="12-3">
									<span slot="title"></span>
								</el-menu-item>
							</router-link>
						</el-submenu>

						<!-- <router-link to="/activity" v-if="role==='' || role===''">
								<el-menu-item index="13">
										<i class="el-icon-document"></i>
										<span slot="title"></span>
								</el-menu-item>
						</router-link> -->

						<router-link to="/employee" v-if="role==='' || role===''">
							<el-menu-item index="14">
								<i class="el-icon-document"></i>
								<span slot="title"></span>
							</el-menu-item>
						</router-link>

						<!-- <router-link to="/employee" v-if="role==='' || role===''">
							<el-menu-item index="14">
								<i class="el-icon-document"></i>
								<span slot="title"></span>
							</el-menu-item>
						</router-link> -->

					</el-menu>
				</el-col>
			</el-row>
		</el-aside>
	</div>
</template>

<script>
export default {
  name: "SideMenu",
  data() {
    return {};
  },
  computed: {
    role: function() {
      //    
      return this.getRoleSession();
    },
    activeMenu: function() {
      const route = this.$route.fullPath;
      if (route.match("store")) {
        // -
        return "1";
      } else if (route.match("principal")) {
        //-
        return "2";
      } else if (route.match("chart")) {
        // -
        return "3";
      } else if (route.match("reception")) {
        // -
        return "4";
      } else if (route.match("operator")) {
        // -
        return "5";
      } else if (route.match("appointment")) {
        // |-
        return "6";
      } else if (route.match("consume-change")) {
        // |-
        return "7-2"; //consume-change
      } else if (route.match("consume")) {
        // |-
        return "7-1";
      } else if (route.match("customer-card-change")) {
        // |-
        return "8-4"; //customer-card-change
      }else if (route.match("customer-activity")) {
        // |-
        return "8-3"; //customer-activity
      } else if (route.match("customer-card")) {
        // |-
        return "8-2"; //customer-card
      } else if (route.match("customer")) {
        // |-
        return "8-1";
      }else if (route.match("stock-type")) {
        // |-
        return "9-1";
      } else if (route.match("stock-amount")) {
        // |-
        return "9-3"; // stock-amount
      } else if (route.match("stock")) {
        // |-
        return "9-2";
      } else if (route.match("project-type")) {
        // |-
        return "10-1";
      } else if (route.match("project")) {
        // |-
        return "10-2";
      } else if (route.match("member-card")) {
        // |-
        return "11";
      } else if (route.match("product-type")) {
        // |-
        return "12-1";
      } else if (route.match("product-amount")) {
        // |-
        return "12-3";// product-amount
      } else if (route.match("product")) {
        // |-
        return "12-2";
      } else if (route.match("activity")) {
        // |-
        return "13";
      } else if (route.match("employee")) {
        //  |-
        return "14";
      } else if (route.match("employee-grade")) {
        //  |-
        return "15";
      }
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
.SideMenu {
  position: fixed;
}
</style>


